html {
  scroll-behavior: smooth;
}

.App {
  width: 100vw;
  color: white;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  left: 50%;
  text-align: center;
  height: auto;
  overflow: auto;

}

.App-bg {
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.923); /* Black w/opacity/see-through */
  color: white;
  background-image: url(/React-Portfolio/static/media/laptop.450fd789.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  overflow: auto;

}

/* *Header */
.Header {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10vh auto;
    color: black;
    flex-direction: column;
    text-shadow: 1px 1px 1px #524e42ff;
    background: rgba(200, 202, 204, 0.8);
    width: 100vw;
}

#_headerText {
    font-size: 4vw;

}

._titleFont {
    font-family: 'Lora', serif;
    box-sizing: border-box;
}

._textFont {
    font-family: 'Lora', serif;
    box-sizing: content-box;
    font-size: 2vw;
}

/* do I need an About.css to follow convention? */
.AboutComponent {
    background: rgba(50, 54, 57, 0.8);
    margin-bottom: 20vh;
    padding-top: 5vh;
    padding-bottom: 5vh;
    padding-left: 20vw;
    padding-right: 20vw;
}

#profileImg {
    width: 20vw;
}

.routes-container {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
}

.routes-link {
    font-family: 'Lora', serif;
    background: rgba(50, 54, 57, 0.8);
    color: white;

    width: 100vw;
    text-decoration: none;
    padding: 0px;
    font-size: 1vw;
}

.routes-link:hover {
    text-decoration: underline;
    color: white;

}

.routes-link:focus {
    text-decoration: underline;
    border: 1px solid white;
}

.Footer {
    padding-bottom: 6vh;
    height: 5vh;
    background-color: black;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.footerText:hover {
    cursor: pointer;
}

.footerText {
    font-size: 20px;
}

Button {
    margin-top: 5vh;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

/*! Modal things */
#emailForm {
    display: flex;
    flex-direction: column;
}

#message {
    height: 30vh;
}
.pdf {
    width: 80%;
    height: 100vh;
    
}

.centered {
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

#_openButton {
    width: -webkit-max-content;
    width: max-content;
    cursor: pointer;
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 1vw;
}

#_closeButton {
    margin-top: 5px;
    margin-bottom: 5px;
    cursor: pointer;
}

.Resume {
    background: rgba(50, 54, 57, 0.8);
    margin-bottom: 20vh;
}
p {
    font-family: 'Lora', serif;
}


.bigShield {
    width: 10vw;
    border: 2px solid transparent;
    margin-bottom: 5vh;
    box-sizing: border-box;
    margin-right: 5vw;
}
.smallShield {
    width: 7vw;
    border: 2px solid transparent;
    margin-bottom: 5vh;
    margin-right: 5vw;
    box-sizing: border-box;
}

.smallShield:hover {
    display: none;
}

.bigShield:hover {
    cursor: pointer;
    border: 2px solid white;
}
